import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Manuales } from '../models/manuales';
import* as vglobales from 'globales'
@Injectable({
  providedIn: 'root'
})
export class ManualesService {
  url = vglobales.lurl+'/api/manuales/';

  constructor(private http: HttpClient) { }

  getManuales(): Observable<any> {
    return this.http.get(this.url);
  }

  eliminarManual(id: string): Observable<any> {
    return this.http.delete(this.url + id);
  }

  guardarManuales(manuales: Manuales): Observable<any> {
    return this.http.post(this.url, manuales);
  }

  obtenerManual(id: string): Observable<any> {
    return this.http.get(this.url + id);
  }
}
