import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Notificaciones } from '../models/notificaciones';
import * as vglobales from 'globales';

@Injectable({
  providedIn: 'root'
})
export class NotificacionesService {
  url = vglobales.lurl+'/api/notificaciones/';

  constructor(private http: HttpClient) { }

  getNotificaciones(): Observable<any> {
    return this.http.get(this.url);
  }

  eliminarNotificaciones(id: string): Observable<any> {
    return this.http.delete(this.url + id);
  }

  guardarNotificaciones(notificaciones: Notificaciones): Observable<any> {
    return this.http.post(this.url, notificaciones);
  }

  obtenerNotificacion(id: string): Observable<any> {
    return this.http.get(this.url + id);
  }

}
