import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Clientes } from 'src/app/protected/models/clientes';
import { ClientesService } from 'src/app/protected/services/clientes.service';


@Component({
  selector: 'app-clientes',
  templateUrl: './clientes.component.html',
  styleUrls: ['./clientes.component.css']
})
export class ClientesComponent implements OnInit { 
  listClientes: Clientes[] = [];
  

  constructor(private _clientesService: ClientesService,
    private toastr: ToastrService) { }

  ngOnInit(): void {
    this.obtenerClientes();
  }


  obtenerClientes() {
    this._clientesService.getClientes().subscribe(data => {
      console.log(data);
      this.listClientes = data;
    }, error => {
      console.log(error);
    })
  }

  eliminarClientes(id: any) {
    this._clientesService.eliminarClientes(id).subscribe(data => {
      this.toastr.error('El producto fue eliminado con exito' ,'Producto Eliminado');
      this.obtenerClientes();
    }, error => {
      console.log(error);
    })
  }



}

