import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Almacen } from '../models/almacen';
import * as vglobales from 'globales';

@Injectable({
  providedIn: 'root'
})
export class AlmacenService {
  url = vglobales.lurl + '/api/almacen/';

  constructor(private http: HttpClient) { }

  getAlmacen(): Observable<any> {
    return this.http.get(this.url);
  }

  eliminarAlmacen(id: string): Observable<any> {
    return this.http.delete(this.url + id);
  }

  guardarAlmacen(almacen: Almacen): Observable<any> {
    return this.http.post(this.url, almacen);
  }

  obtenerAlmacen(id: string): Observable<any> {
    return this.http.get(this.url + id);
  }
}







