import { Component, OnInit } from '@angular/core';
////importaciones necesaqrios, varian

import { ToastrService } from 'ngx-toastr';
import { Notificaciones } from 'src/app/protected/models/notificaciones';
import { NotificacionesService } from 'src/app/protected/services/notificaciones.service';

@Component({
  selector: 'app-listar-notificaciones',
  templateUrl: './listar-notificaciones.component.html',
  styleUrls: ['./listar-notificaciones.component.css']
})
export class ListarNotificacionesComponent implements OnInit {

  listNotificaciones: Notificaciones[] = [];
  

  constructor(private _notificacionesService: NotificacionesService,
    private toastr: ToastrService) { }

  ngOnInit(): void {
    this.obtenerNotificaciones();
  }


  obtenerNotificaciones() {
    this._notificacionesService.getNotificaciones().subscribe(data => {
      console.log(data);
      this.listNotificaciones = data;
    }, error => {
      console.log(error);
    })
  }

  eliminarNotificacion(id: any) {
    this._notificacionesService.eliminarNotificaciones(id).subscribe(data => {
      this.toastr.error('La notificación fue eliminada con exito' ,'Notificación Eliminado');
      this.obtenerNotificaciones();
    }, error => {
      console.log(error);
    })
  }




}








