import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Prestamos } from 'src/app/protected/models/prestamos';
import { PrestamosService } from 'src/app/protected/services/prestamos.service';


@Component({
  selector: 'app-prestamos',
  templateUrl: './prestamos.component.html',
  styleUrls: ['./prestamos.component.css']
})
export class PrestamosComponent implements OnInit { 
  listPrestamos: Prestamos[] = [];
  

  constructor(private _prestamosService: PrestamosService,
    private toastr: ToastrService) { }

  ngOnInit(): void {
    this.obtenerPrestamos();
  }


  obtenerPrestamos() {
    this._prestamosService.getPrestamos().subscribe(data => {
      console.log(data);
      this.listPrestamos = data;
    }, error => {
      console.log(error);
    })
  }

  eliminarPrestamos(id: any) {
    this._prestamosService.eliminarPrestamos(id).subscribe(data => {
      this.toastr.error('El prestamo fue eliminado con exito' ,'prestamo Eliminado');
      this.obtenerPrestamos();
    }, error => {
      console.log(error);
    })
  }



}

