import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Almacen } from 'src/app/protected/models/almacen';
import { AlmacenService } from 'src/app/protected/services/almacen.service';

@Component({
  selector: 'app-ventas',
  templateUrl: './ventas.component.html',
  styleUrls: ['./ventas.component.css']
})

export class VentasComponent implements OnInit {
  listAlmacen: Almacen[] = [];


  constructor(private _almacenService: AlmacenService,
    private toastr: ToastrService) { }

  ngOnInit(): void {
    this.obtenerAlmacen();
  }


  obtenerAlmacen() {
    this._almacenService.getAlmacen().subscribe(data => {
      console.log(data);
      this.listAlmacen = data;
    }, error => {
      console.log(error);
    })
  }

  eliminarAlmacen(id: any) {
    this._almacenService.eliminarAlmacen(id).subscribe(data => {
      this.toastr.error('El producto fue eliminado con exito' ,'Producto Eliminado');
      this.obtenerAlmacen();
    }, error => {
      console.log(error);
    })
  }



}

