import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Prestamos } from '../models/prestamos';
import * as vglobales from 'globales';

@Injectable({
  providedIn: 'root'
})
export class PrestamosService {
  url = vglobales.lurl + '/api/prestamos/';

  constructor(private http: HttpClient) { }

  getPrestamos(): Observable<any> {
    return this.http.get(this.url);
  }

  eliminarPrestamos(id: string): Observable<any> {
    return this.http.delete(this.url + id);
  }

  guardarPrestamos(prestamos: Prestamos): Observable<any> {
    return this.http.post(this.url, prestamos);
  }

  obtenerPrestamos(id: string): Observable<any> {
    return this.http.get(this.url + id);
  }
}

