

import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Usuarios } from 'src/app/protected/models/usuarios';
import { UsuariosService } from 'src/app/protected/services/usuarios.service';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-crear-usuarios',
  templateUrl: './crear-usuarios.component.html',
  styleUrls: ['./crear-usuarios.component.css']
})
export class CrearUsuariosComponent implements OnInit {

  usuariosForm: FormGroup;
  titulo = 'Crear usuarios';
  id: string | null;
  constructor(private fb: FormBuilder,
              private router: Router,
              private toastr: ToastrService,
              private _usuariosService: UsuariosService,
              private aRouter: ActivatedRoute) { 
    this.usuariosForm = this.fb.group({
      nombre: ['', Validators.required],
      aoellido: ['', Validators.required],
      correo: ['', Validators.required],
      contrasena: ['', Validators.required]
     
    })
    this.id = this.aRouter.snapshot.paramMap.get('id');
  }

  ngOnInit(): void {
    this.esEditar();
  }



  esEditar() {

    if(this.id !== null) {
      
      this._usuariosService.obtenerUsuario(this.id).subscribe(data => {
        this.usuariosForm.setValue({
          name: data.name,
          email: data.email,
          password: data.password,
          area: data.area
        
        })
      })
    }
  }


  actualizar(){

    
  }


}



