import { Component, OnInit } from '@angular/core';
////importaciones necesaqrios, varian

import { ToastrService } from 'ngx-toastr';

import { Videos } from 'src/app/protected/models/videos';
import { VideosService } from 'src/app/protected/services/videos.service';


@Component({
  selector: 'app-listar-videos',
  templateUrl: './listar-videos.component.html',
  styleUrls: ['./listar-videos.component.css']
})
export class ListarVideosComponent implements OnInit {

  listVideos: Videos[] = [];
  

  constructor(private _videosService: VideosService,
    private toastr: ToastrService) { }

  ngOnInit(): void {
    this.obtenerVideos();
  }


  obtenerVideos() {
    this._videosService.getVideos().subscribe(data => {
      console.log(data);
      this.listVideos = data;
    }, error => {
      console.log(error);
    })
  }

  eliminarVideos(id: any) {
    this._videosService.eliminarVideos(id).subscribe(data => {
      this.toastr.error('El videos fue eliminado con exito' ,'Video Eliminado');
      this.obtenerVideos();
    }, error => {
      console.log(error);
    })
  }

}
