import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Misdocumentos } from '../models/misdocumentos';
import * as vglobales from 'globales';


@Injectable({
  providedIn: 'root'
})
export class MisdocumentosService {
  url = vglobales.lurl + '/api/misdocumentos/';

  constructor(private http: HttpClient) { }

  getMisdocumentos(): Observable<any> {
    return this.http.get(this.url);
  }

  eliminarMisdocumentos(id: string): Observable<any> {
    return this.http.delete(this.url + id);
  }

  guardarMisdocumentos(misdocumentos: Misdocumentos): Observable<any> {
    return this.http.post(this.url, misdocumentos);
  }

  obtenerMidocumento(id: string): Observable<any> {
    return this.http.get(this.url + id);
  }
}










