import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Videos } from 'src/app/protected/models/videos';
import { VideosService } from 'src/app/protected/services/videos.service';


@Component({
  selector: 'app-crear-videos',
  templateUrl: './crear-videos.component.html',
  styleUrls: ['./crear-videos.component.css']
})
export class CrearVideosComponent implements OnInit {

  videosForm: FormGroup;
  titulo = 'Crear videos';
  id: string | null;
  constructor(private fb: FormBuilder,
              private router: Router,
              private toastr: ToastrService,
              private _videosService: VideosService,
              private aRouter: ActivatedRoute) { 
    this.videosForm = this.fb.group({
      nombre: ['', Validators.required],
      link: ['', Validators.required],
      descripcion: ['', Validators.required]
     
    })
    this.id = this.aRouter.snapshot.paramMap.get('id');
  }

  ngOnInit(): void {
    this.esEditar();
  }

  agregarVideos() {

    const VIDEOS: Videos = {
      nombre: this.videosForm.get('nombre')?.value,
      link: this.videosForm.get('link')?.value,
      descripcion: this.videosForm.get('descripcion')?.value
  
    }

    console.log(VIDEOS);
    this._videosService.guardarVideos(VIDEOS).subscribe(data => {
      this.toastr.success('El video fue registrado con exito!', 'video Registrado!');
      this.router.navigate(['/']);
    }, error => {
      console.log(error);
      this.videosForm.reset();
    })

  
  }

  esEditar() {

    if(this.id !== null) {
      this.titulo = 'Editar producto';
      this._videosService.obtenerVideo(this.id).subscribe(data => {
        this.videosForm.setValue({
          nombre: data.nombre,
          link: data.link,
          descripcion: data.descripcion
        
        })
      })
    }
  }

}





