import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Clientes } from '../models/clientes';
import * as vglobales from 'globales';

@Injectable({
  providedIn: 'root'
})

export class ClientesService {
  url = vglobales.lurl + '/api/clientes/';

  constructor(private http: HttpClient) { }

  getClientes(): Observable<any> {
    return this.http.get(this.url);
  }

  eliminarClientes(id: string): Observable<any> {
    return this.http.delete(this.url + id);
  }

  guardarClientes(clientes: Clientes): Observable<any> {
    return this.http.post(this.url, clientes);
  }

  obtenerClientes(id: string): Observable<any> {
    return this.http.get(this.url + id);
  }
}







