import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from '../auth/pages/login/login.component';
import { CrearManualesComponent } from './administrador/crear-manuales/crear-manuales.component';
import { CrearUsuariosComponent } from './administrador/crear-usuarios/crear-usuarios.component';
import { DashadminComponent } from './administrador/dashadmin/dashadmin.component';
import { ListarNotificacionesComponent } from './administrador/listar-notificaciones/listar-notificaciones.component';
import { ListarNotificacionesadminComponent } from './administrador/listar-notificacionesadmin/listar-notificacionesadmin.component';
import { ListarUsuariosComponent } from './administrador/listar-usuarios/listar-usuarios.component';
import { ListarVideosComponent } from './administrador/listar-videos/listar-videos.component';
import { ClientesComponent } from './workzone/clientes/clientes.component';
import { CrearClientesComponent } from './workzone/crear-clientes/crear-clientes.component';
import { CrearMisdocumentosComponent } from './workzone/crear-misdocumentos/crear-misdocumentos.component';
import { CrearPrestamosComponent } from './workzone/crear-prestamos/crear-prestamos.component';
import { MisdocumentosComponent } from './workzone/misdocumentos/misdocumentos.component';
import { MisnotificacionesComponent } from './workzone/misnotificaciones/misnotificaciones.component';
import { PrestamosComponent } from './workzone/prestamos/prestamos.component';
import { VentasComponent } from './workzone/ventas/ventas.component';

const routes: Routes = [

  {
    path: '',
    children: [
      { path: '', component: LoginComponent },
      { path: 'clientes', component: ClientesComponent },
      { path: 'prestamos', component: PrestamosComponent },
    
      { path: 'dashadmin', component: DashadminComponent },
      { path: 'crear-usuarios', component: CrearUsuariosComponent },
      { path: 'listar-notificaciones', component: ListarNotificacionesComponent },
      { path: 'listar-notificacionesadmin', component: ListarNotificacionesadminComponent },
      { path: 'listar-usuarios', component: ListarUsuariosComponent },
      { path: 'listar-videos', component: ListarVideosComponent },
      { path: 'crear-manuales', component: CrearManualesComponent },
     

       { path: 'misdocumentos', component: MisdocumentosComponent },
      { path: 'misnotificaciones', component: MisnotificacionesComponent },
      
      { path: 'ventas', component: VentasComponent },


      { path: 'crear-misdocumentos', component: CrearMisdocumentosComponent },
      { path: 'crear-clientes', component: CrearClientesComponent },
      { path: 'crear-prestamos', component: CrearPrestamosComponent },


      { path: '**', redirectTo: '' },
    ]
  }

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProtectedRoutingModule { }
