import { Component, OnInit } from '@angular/core';



import { ToastrService } from 'ngx-toastr';
import { Misdocumentos } from 'src/app/protected/models/misdocumentos';
import { MisdocumentosService } from 'src/app/protected/services/misdocumentos.service';

@Component({
  selector: 'app-misdocumentos',
  templateUrl: './misdocumentos.component.html',
  styleUrls: ['./misdocumentos.component.css']
})
export class MisdocumentosComponent implements OnInit {

  listMisdocumentos: Misdocumentos[] = [];
  

  constructor(private _misdocumentosService: MisdocumentosService,
    private toastr: ToastrService) { }

  ngOnInit(): void {
    this.obtenerManuales();
  }


  obtenerManuales() {
    this._misdocumentosService.getMisdocumentos().subscribe(data => {
      console.log(data);
      this.listMisdocumentos = data;
    }, error => {
      console.log(error);
    })
  }

  eliminarManual(id: any) {
    this._misdocumentosService.eliminarMisdocumentos(id).subscribe(data => {
      this.toastr.error('El documento fue eliminado con exito' ,'Manual Eliminado');
      this.obtenerManuales();
    }, error => {
      console.log(error);
    })
  }



}

