import { Component } from '@angular/core';
import { MessageService } from 'src/app/services/message.service';
import Swal from 'sweetalert2';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
@Component({
  selector: 'app-dashadmin',
  templateUrl: './dashadmin.component.html',
  styleUrls: ['./dashadmin.component.css']
})
export class DashadminComponent {


  formulario: UntypedFormGroup = this.fb.group({
    nombre:     [''],
    asunto:    [''],
    email: [''],
    mensaje:     [''],

  });

  constructor(private fb: UntypedFormBuilder,
    public _MessageService:  MessageService,
    ) {
 }



correo() {
  const { nombre, asunto, email, mensaje} = this.formulario.value;

  this._MessageService.sendMessage( nombre, asunto, email, mensaje)
  .subscribe(() => {

    Swal.fire('Formulario de contacto', 'Mensaje enviado correctamente', 'success');
  });
  }}
  