import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Videos } from '../models/videos';
import * as vglobales from 'globales';

@Injectable({
  //se cambio root por any
  providedIn: 'any'
})
export class VideosService {
  url = vglobales.lurl+'/api/videos/';

  constructor(private http: HttpClient) { }

  getVideos(): Observable<any> {
    return this.http.get(this.url);
  }

  eliminarVideos(id: string): Observable<any> {
    return this.http.delete(this.url + id);
  }

  guardarVideos(videos: Videos): Observable<any> {
    return this.http.post(this.url, videos);
  }

  obtenerVideo(id: string): Observable<any> {
    return this.http.get(this.url + id);
  }
}
